/*
  Project variables
  =================
*/


/*
  Flex Sass grid generator variables
  ==================================

  [flex-sass-grid-generator](https://github.com/Enkil/flex-sass-grid-generator)

*/

$base-font-size:              16;
$grid-columns:                12 !default;
$grid-gutter-width:           1.25rem !default; // 20px

$screen-xs:                   0; // Extra small screen / phone
$screen-sm:                   480px; // Small screen / phone
$screen-md:                   768px; // Medium screen / tablet
$screen-lg:                   992px; // Large screen / desktop

$grid-breakpoints: (
        xs:                   0,
        sm:                   540px,
        md:                   800px,
        lg:                   1024px
) !default;


$container-max-widths: (
        sm:                   480px,
        md:                   768px,
        lg:                   960px
) !default;

/*
  Sass MQ variables
  =================

  [sass-mq](https://github.com/sass-mq/sass-mq)
*/

$mq-breakpoints: (
        xs:                   0px,
        sm:                   540px,
        md:                   800px,
        lg:                   1024px,
        xl:                   1280px,
        xxl:                  1900px

) !default;


